import React from 'react';
import WelcomeRects from './WelcomeRects';
import ShowBluePage from './ShowBluePage';
import ShowPinkPage from './ShowPinkPage';
import ShowGreenPage from './ShowGreenPage';
import '../styles/animation-s.scss';

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blue: props.blue,
            green: props.green,
            pink: props.pink,
            showRects: true,
            welcome: props.welcome,
            counter: props.counter
        }
        this.handleClickPink = this.props.pinkClick.bind(this);
        this.handleClickBlue = this.props.blueClick.bind(this);
        this.handleClickGreen = this.props.greenClick.bind(this);
    }

    handleClickPink(event) {
        console.log('Pink');
        this.setState({
            blue: false,
            green: false,
            pink: true,
            welcome: false,
        });
        this.setState((prevState, props) => ({
            counter: prevState.counter + 1
        }));
    }
    // This will reload the page, if that will be needed...
    // switch = () => {
    //     window.location.reload();
    // };
    render() {
        let animate;
        if (this.props.blue) {
            //Blue button is pressed so animate blue on top
            animate = <ShowBluePage
                blue={this.props.blue}
                green={this.props.green}
                pink={this.props.pink}
                welcome={this.props.welcome}
                counter={this.props.counter} />

        } else if (this.props.green) {
            //Green button is pressed so animate green on top
            animate = <ShowGreenPage
                blue={this.props.blue}
                green={this.props.green}
                pink={this.props.pink}
                welcome={this.props.welcome}
                counter={this.props.counter} />

        } else if (this.props.pink) {
            //Pink button is pressed so animate pink on top
            animate = <ShowPinkPage
                blue={this.props.blue}
                green={this.props.green}
                pink={this.props.pink}
                welcome={this.props.welcome}
                counter={this.props.counter}
            />

        } else {
            animate =
                <WelcomeRects
                    blue={this.props.blue}
                    green={this.props.green}
                    pink={this.props.pink}
                    welcome={this.props.welcome}
                    counter={this.props.counter}
                    pinkClick={this.handleClickPink}
                    blueClick={this.handleClickBlue}
                    greenClick={this.handleClickGreen}
                    media={this.props.media}
                />
        }

        return (
            <div>
                {animate}
            </div>
        )
    }
}

export default Main;