import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/animation-s.scss';
import Raster from '../assets/image/raster.png';
import mediaEurPic from '../assets/image/Kreativa_Europa_MEDIA.png';

class ShowGreenPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blue: props.blue,
            green: props.green,
            pink: props.pink,
            showRects: true,
            welcome: props.welcome,
            counter: props.counter
        }
    }

    render() {
        let animate = <div></div>;
        let greenText = <div>
            <div className="container static-page">
                <div className="row" id="text-row">
                    <div className="col-12">
                        <div className="information-text">Delprogrammet MEDIA vänder sig till aktörer inom den audiovisuella sektorn (film, tv och dataspel). Stöd kan sökas av produktionsbolag, spelutvecklare, utbildningsorganisationer, distributörer, filmfestivaler och andra organisationer inom sektorn.</div>
                    </div>
                </div>
                <div className="row justify-content-around align-items-center">
                    <div className="col-8">
                        <a href="https://www.filminstitutet.se/sv/sok-stod/kreativa-europa/" className="static-page-link">Kreativa Europa MEDIA</a>
                    </div>
                    <div className="col-4">
                    <a href="https://www.filminstitutet.se/sv/sok-stod/kreativa-europa/"><img src={mediaEurPic} className="icon-pic" alt="not loading"></img></a>
                    </div>
                </div>
            </div>
        </div>
        /**
         * check if first time pressing a link. 
         * If this is the case animate 
         * from: split
         * to : center
         * -------
         * ELSE animate
         * from: center
         * to center
         * and change background-color
         * from: full
         * to: transparent
         */
        // if(props.welcome){
        //     animate = {

        //     }
        // } else {

        // }

        if (this.props.counter > 1) {
            animate = <div>
                <div className="rect-anim static-green-rect" id="greenRect">
                    <div className="raster">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                    {greenText}
                </div>
            </div>
        } else {
            animate = <div>
                <CSSTransition
                    in={this.props.green}
                    timeout={0}
                    classNames="pink-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim pink" id="pinkRect">
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.green}
                    timeout={0}
                    classNames="blue-transition-under"
                    unmountOnExit
                    appear
                    // This will create an animation from full color as in prev state to transparent
                    //onEntered={()=> document.getElementById("abc").hidden = true}
                    exit
                >
                    <div className="rect-anim blue" id="blueRect">
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.green}
                    timeout={0}
                    classNames="green-transition-ontop"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim green" id="greenRect">
                        <div className="raster">
                            <img src={Raster} width="100%" height="100%" alt="" ></img>
                        </div>
                        {greenText}
                    </div>
                </CSSTransition>
            </div>
        }
        return (
            <div>
                {animate}
            </div>
        );
    }
}

export default ShowGreenPage;





